import { string } from "prop-types";

import styles from "./Profile.scss";

const Profile = props => (
  <a target="_blank" href={props.linkedin} title={props.name} className={styles.profile}>
    <img src={props.image} />
  </a>
);

Profile.propTypes = {
  name: string.isRequired,
  image: string.isRequired,
  linkedin: string
};

export default Profile;
